body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #2A0944;
  background-image: url("https://www.transparenttextures.com/patterns/diagonal-striped-brick.png");   
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  width: 368px;
  min-height: 580px;
  margin: 3.5% auto;
  background-color: #002B5B;
  background-color: #008c8c;
  background-image: url("https://www.transparenttextures.com/patterns/worn-dots.png");
  border-radius: 1rem;
  padding: 1rem 1.4rem;
  box-shadow: 9px 1px 18px 0px rgba(0,0,0,0.57);
}


@media screen and (max-width:400px) {
  .container{
    margin:20% auto;
  }
}
.container h1 {
  margin: 0;
  font-family: 'Permanent Marker';
  color:white;
  text-align: center;
  margin-bottom: 2.5rem;
}

.input-add-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
}

.input-add-wrapper input {
  width: 75%;
  padding: 0.5rem;
  font-size: 1rem;
  background-color: transparent;
  outline: none;
  border: none;
  color:white;
  border-bottom: 2px dashed white;
}

.input-add-wrapper input::placeholder{
  color:#E8F9FD;
}

.btn {
  background-color: transparent;
  padding: 0.6rem 1rem;
  font-family: 'Krub';
  color:black;
  font-weight: 300;
  background-color: yellow;
  border-radius: 0.5rem;
}

.task-list ul {
  position: relative;
  padding: 0;
  margin: 0;
  font-family: 'Krub';
  color:white;
  font-size: 1.5rem;
}

.task-list ul li {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.task-list ul li input{
  padding: 0;
  margin: 0;
  display: none;
}



.task-list ul li label{
  display: inline;
  cursor: pointer;
  
  font-size: 1.2rem;
  padding-left: 1.8rem;
  user-select: none;
  width: 80%;
  word-wrap: break-word;
}

label::before {
  content: "";
  position: absolute;
  display: inline-block;
  height: 1rem;
  width: 1rem;
  left:0;
  top:23%;
  vertical-align: bottom;
  margin-right: 0.8rem;
  border-radius: 50%;
  border: 2px solid yellow;
}



.task-list input:checked + label::before {
  background-color: yellow;
}


.delete-btn {
  cursor: pointer;
}

.delete-btn:hover{
  color: red;
}
